import {
    Box,
    Image,
    Text,
    Flex,
    Spacer,
} from "@chakra-ui/react"
import { motion } from "framer-motion"
import { useState } from "react"
import Quote from "./Quote"
import hide from "../images/hide.png"


export default function ImageCard({book, controlsText, controlsPic}){
    const [show, setShow] = useState(false)

    const image={
        hidden: {
            opacity:0,
            transition:{
                delay:0.3
            }
        },
        visible:{
            opacity:1,
            transition:{
                delay:0.3
            }
        }

    }

    return(
        <Box w='100%' h='60%' bgImage={book.image} bgRepeat='no-repeat' bgSize="cover" roundedTop="50px" >
            <Box h='100%' backdropFilter='auto' backdropBlur='5px' backdropContrast="80%" backdropBrightness="50%"  roundedTop="50px">
                <Quote quote={book.quote} controlsText={controlsText}/>
                <Box h='94%' position="relative" zIndex={1} as={motion.div} variants={image} initial="hidden" animate={controlsPic}>
                    <Image h='100%' visibility={show} src={book.image} pt='10%' />      
                </Box>
                <Flex overflow='hidden'>
                    <Text align="left" pl='10px' fontSize="20px" color="#FAFAFA"> <strong>{book.title},</strong> {new Date().getFullYear() - book.year}</Text>
                    <Spacer />
                    <Image src={hide} h='30px' pr='2px' position="relative" zIndex={1} onClick={() => show? (setShow(false), controlsPic.start("hidden")) : (setShow(true), controlsPic.start("visible")) } />
                </Flex>
            </Box>
        </Box>
    )
}