import { motion } from "framer-motion";
import { Text } from "@chakra-ui/layout";

export default function Quote({quote, controlsText}) {


    let line = quote

    const sentence = {
      hidden: {opacity:1},
      visible: {
        opacity: 1,
        transition: {
          delay: 1,
          staggerChildren: 3/line.length
          }
        }
    }
  
    const letter = {
      hidden: {opacity: 0},
      visible: {opacity:1}
    }
    
    return(
      <Text as={motion.div} position="absolute" zIndex={0} fontSize="20px" color='white' h='100%' py='25%' px='15%'>
        <Text align='left' fontSize='40px' fontWeight="bold">
          
        </Text>
        <motion.p variants={sentence} initial="hidden" animate={controlsText} >
          {line.split("").map((char,index) => {
            return (
              <motion.span key={char+"-"+index} variants={letter}>
                {char}
              </motion.span>
            )
          })}
        </motion.p>
        <Text align='right' fontSize='40px' fontWeight="bold">
          
        </Text>
      </Text>
    )
  }