import { motion } from "framer-motion"
import { Box, VStack, Text } from "@chakra-ui/react"
import video from '../images/read.mp4'

export default function LandingPage({setStart}){

    const textVariants = {
        hidden: {
            x: "-150%",
        },
        visible: (custom) => ({
            x: 0,
            transition: { delay:custom, ease: [0.455, 0.03, 0.515, 0.955], duration: 2 }
        })
    }


    return(
            <Box h='95vh' py="2.5vh"  w={window.innerWidth > 400 ? "400px" : window.innerWidth - 15}>
                <Box h='91vh' position='absolute' zIndex={0} as='video' src={video} objectFit='cover' rounded="50px" autoPlay muted loop filter='auto' blur='3px' contrast="90%" brightness="40%" boxShadow='15px 10px 40px #121212' />
                <VStack position='relative' zIndex={1} overflow='hidden' w="100%" h='100%' rounded="50px" > 
                    <Box h='30%' />
                    <Box overflow='hidden' pl='5px' borderLeft="2px" borderTopLeftRadius="25px" borderColor="white" align='left' >
                        <Text fontSize='48px' mb='-10px' color='#FBFBFB' as={motion.div} variants={textVariants} custom={0.5} initial='hidden' animate="visible">
                            Readr
                        </Text>
                        <Text fontSize='16' pl='2px' color='#FBFBFB' as={motion.div} variants={textVariants} custom={1.5} initial='hidden' animate="visible">
                            Meet new books
                        </Text>
                        <Text fontSize='16' pl='2px' color='#FBFBFB' as={motion.div} variants={textVariants} custom={2.5} initial='hidden' animate="visible">
                            Create new chapters
                        </Text>
                    </Box>
                    <Box h='35%' />
                        <Text fontSize='16' pl='2px' color='#FBFBFB' as={motion.div} animate={{x:[0,20,0], transition:{repeat:"Infinity", repeatDelay:2, duration:2}}}>
                            Swipe to begin
                        </Text>
                </VStack>
            </Box>
    )
}