import { useState, react} from 'react';
import {
  Box,
} from '@chakra-ui/react';
import Library from './components/Library';
import LandingPage from './components/LandingPage';
import { motion, useAnimation } from 'framer-motion';

function App() {
  const [start, setStart] = useState(false)
  const [bookIndex, setBookIndex] = useState(0)
  const [dragStart,setDragStart] = useState(0)
  const [dragEnd,setDragEnd] = useState(0)

  const controlsText = useAnimation()
  const controlsBook = useAnimation()

  const handleSwipe = () => {
    if (Math.abs(dragEnd - dragStart) > 200 && start) { //go to next book
      console.log("Swiped!")
      setBookIndex(bookIndex+1)
      controlsText.start("visible")
      controlsBook.start("visible")
    }else if (Math.abs(dragEnd - dragStart) > 200) { //leave the landing page
      console.log("Started!")
      setStart(true)
      setBookIndex(bookIndex)
      controlsText.start("visible")
      controlsBook.start("visible")
  }
}


  return (
      <Box h='100vh' w='100vw' align="center" overflow="hidden" bg="#e8e2d9">
        <Box  as={motion.div} drag='x' dragSnapToOrigin dragConstraints={{left: 0, right: 0}} onDragStart={(event,info) => {setDragStart(info.point.x)}} onDragEnd={(event,info) => {setDragEnd(info.point.x); handleSwipe()}}>
          {start? <Library bookIndex={bookIndex} controlsBook={controlsBook} controlsText={controlsText} /> : <LandingPage setStart={setStart} />}
        </Box>
      </Box>
  );
}

export default App;
