import { motion, useAnimation } from "framer-motion";
import {
    Box,
} from "@chakra-ui/react"
import Book from "./Book";
import books from './books.json';

function shuffle(sourceArray) {
    for (var i = 0; i < sourceArray.length - 1; i++) {
        var j = i + Math.floor(Math.random() * (sourceArray.length - i));

        var temp = sourceArray[j];
        sourceArray[j] = sourceArray[i];
        sourceArray[i] = temp;
    }
    return sourceArray;
}

shuffle(books) //randomise books

export default function Library({bookIndex, controlsText, controlsBook}){
    const controlsPic = useAnimation()

    const bookVariants = {
        hidden:{
            opacity: 0
        },
        visible:{
            opacity:1,
            transition:{
                duration:3
            }
        }
    }
    return(
        <Box as={motion.div} variants={bookVariants} animate={controlsBook}>
            <Book book={books[bookIndex%books.length]} controlsText={controlsText} controlsPic={controlsPic}/>
        </Box>
    )
}