import {
    Box,
    VStack,
    Text,
    Image,
} from "@chakra-ui/react"
import ImageCard from "./ImageCard";
import download from "../images/download.png"


export default function Book({book, controlsText,controlsPic}) {
    return(
        <Box h='95vh' py="2.5vh"  w={window.innerWidth > 400 ? "400px" : window.innerWidth - 15}>
            <VStack  w="100%" h='100%' bg='#FFFFFF' rounded="50px" boxShadow='15px 10px 40px #121212' > 
                <ImageCard book={book} controlsText={controlsText} controlsPic={controlsPic} />
                <Box h='30%' w='100%' align="left" px='10px' py='3px'  overflow="auto">
                    <Text color="#513f39" fontSize="20px" fontWeight="bold">
                        Synopsis
                    </Text>
                    <Text fontSize="14px" align='justify'>
                        {book.synopsis}
                    </Text>
                </Box>
                <Box w='100%' onClick={() => window.open(book.download)}>
                    <Image src={download} h='50px' />
                </Box>
            </VStack>
        </Box>
    )
}

